// DEFAULTS
.main-container {
  display: flex;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.main-container::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.main-container {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

// Container
.container {
  padding: 2rem 1rem;
  width: 100%;
  max-width: var(--mx-width);

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 1rem;
    width: 100%;
  }
}

/* HEADER */
.header {
  border-bottom: 1px solid hsl(var(--text) / 0.08);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  width: 100%;
}

.header .github-link {
  color: hsl(var(--text) / 0.87);
  font-size: 2.5rem;
}

/* FOOTER */
.footer {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 5rem;
}

// SECTION
.section {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 10rem); // 10rem is header + footer height
  min-width: 100vw;
  scroll-snap-align: start;
}

/* HERO */
/* TODO: use shorthand for padding */
.hero {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 2rem;
  padding-top: 2rem;
  width: 100%;
  max-width: var(--mx-width);

  &__head {
    padding-left: 1rem;
    width: 100%;
  }

  &__tagline {
    margin-bottom: 0.5rem;
  }

  &__role {
    font-weight: 700;
  }

  &__summary {
    width: 11.25rem;
  }

  @media screen and (min-width: 700px) {
    padding-bottom: 2rem;

    &__head {
      position: absolute;
      display: flex;
      justify-content: center;
      flex-direction: column;
      margin: 0;
      z-index: 10;
    }
  }
}

/* TILES */
.tiles {
  border: 1px solid hsl(var(--text) / 0.12);
  max-width: 24rem;
  margin: auto;
}

.tile {
  display: flex;
  flex-direction: column;
}

.tile__child {
  display: flex;
}

.tile__child > * {
  flex-basis: 100%;
}

.tile__img {
  width: 100%;
  height: 100%;
}

.tile__box {
  background-color: hsl(var(--text) / 0.005);
  display: flex;
  align-items: end;
}

.tile__box:hover .tile__number {
  color: hsl(var(--accent));
  transition: all 0.25s;
}

.tile__child .desc {
  margin-bottom: 2rem;
}

.tile__info {
  display: flex;
  align-items: end;
  justify-content: space-between;
  padding: 0.5rem;
  width: 100%;
}

.tile__title {
  color: hsl(var(--text) / 0.87);
  font-size: 0.875rem;
  font-weight: 500;
}

.tile__number {
  color: hsl(var(--text) / 0.27);
  font-size: 3rem;
  font-weight: 700;
  line-height: 1;
}
/* END TILES */

// Media
.articles {
  border: 1px solid hsl(var(--text) / 0.12);
  padding: 1rem;
}

.media__title {
  color: hsl(var(--text) / 0.87);
  display: block;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.media__title--link:hover {
  color: hsl(var(--accent));
}

.media__subtitle {
  color: hsl(var(--text) / 0.6);
  line-height: 1.6;
}

.category {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 0.5rem 0;
}

/* TODO: extract color variable to css var (accent-light) */
.category__item {
  color: hsl(14 87% 70%);
  font-size: 0.75rem;
  font-weight: 300;
  text-transform: uppercase;
}

@media screen and (min-width: 768px) {
  .articles {
    width: 55%;
  }
}

// Responsive
@media (max-width: 672px) {
  :root {
    font-size: 75%;
  }
}
